.detail-banner-container{
    margin-top: $topGap;
    padding: $margin;
    .detail-banner-img{
        background-repeat: no-repeat;
        background-position: center;
        height:400px;
        // border-radius: $border-radius;
        border-left: $common-pad solid #000;
        position: relative;
        .detail-banner-shadow{
            background: linear-gradient(to left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 3));
            position: absolute;
            bottom:0px;
            height:100%;
            width:100%;
        }
        .detail-banner-text-container{
            position: absolute;
            width: 80%;
            left: 4%;
            top: 15%;
            .detail-banner-title{
                font-weight: bold;  
                opacity:0.9;
                h1{
                    color:#fff !important;
                }
            }
            .detail-banner-description{
                margin-top: $common-pad;
                border-top: 4px solid rgba(255,255,255,0.2);
                padding-top: 15px;
                p{
                    margin-top:0px;
                    opacity: 0.5;
                    color:#fff !important;
                    font-size: map-get($headerSizes, 3);
                    font-weight: normal;
                    line-height: map-get($headerSizes, 3);
                }
            }
            .detail-banner-techs{
                margin-top: 5px;
                span{
                    display:inline-block;
                    margin-right: 10px;
                    margin-top:0px;
                    opacity: 0.5;
                    color:#fff !important;
                    font-weight: normal;
                    font-size:1.3em;
                    &:hover{
                        opacity: 0.8;
                    }
                }
            }
            .detail-banner-buttons{
                margin-top: 35px;
                .buttonLink{
                    color:#fff;
                    font-size: 1.8em;
                    position: relative;
                    top: 5px;
                    opacity: 0.4;
                    &:hover{
                        opacity: 0.8;
                        cursor: pointer;
                    }
                }
                button{
                    &.demo{
                        padding: 8px 30px;
                        font-weight: bold;
                        color: map-get($primary, 4);
                        font-weight: bold;
                        background: map-get($primary, 1);
                        margin-right: 25px;
                        &:hover{
                            color:#fff;
                            background: #11738f;
                        }
                    }
                    &.next,
                    &.back{
                        color: map-get($linkColor, hover);
                        font-weight: bold;
                        opacity: 0.8;
                        position: relative;
                        font-size: 1.1em;
                        border-radius: 50%;
                        padding: 5px 12px;
                        padding-left:0px;
                        &:hover{
                            opacity: 1;
                            color: map-get($linkColor, hover);
                        }
                        span{
                            top:-1px;
                            padding-left:3px;
                            padding-right:3px;
                            position: relative;
                        }
                    }
                     &.next{
                         margin-left:25px;
                     }

                }
                
            }
        }
    }
}

@media screen and (max-width: $startMobile) {
    .detail-banner-container{
        .detail-banner-description{
            p{
                font-size: map-get($headerSizes, 4) !important;
            }
        }
        .detail-banner-techs{
            margin-top: 5px;
            span{
                font-size: map-get($headerSizes, 4) !important;
            }
        }
        .detail-banner-title{
            h1{
                font-size: map-get($headerSizes, 2);
            }
        }
        .detail-banner-img{
            .detail-banner-text-container{
                top:10%;
            }
        }
    }
}

@media screen and (max-width: $startSMMobile) {
    .detail-banner-container{
        .detail-banner-img{
            .detail-banner-text-container{
                width:90%;
            .detail-banner-title{
                h1{
                    font-size: 1.5em;
                }
            }
            .detail-banner-buttons{
                margin-top: 35px;
                button{
                    &.demo{
                        padding: 8px 15px;
                        margin-right: 25px;
                    }
                }
            }
            }
        }

    }
}