.section-container{
    margin-top: 3.0em;
    margin-bottom: 3.0em;
    padding: $margin;
    padding-bottom: calc(3.0em + 15px);
    border-bottom: 2px solid #eee;
    @extend %fade-in;

    .section-body{
        .section-title{
            margin-bottom: map-get($headerSizes, 2);
            color: map-get($primary, 2);
            font-weight: bold;
            text-align: left;
        }
        .section-text, p{
            font-size: map-get($headerSizes, 5);
            font-weight: normal;
            line-height: 1.4em;;
            b{
                color:#3C7D8F;
            }
            a{
                color:#93bb00;
                text-decoration: none;
            }
            ul{
                margin-left: 15px;
                margin-top: 15px;
                li{
                    font-size: map-get($headerSizes, 5);
                    font-weight: normal;
                    line-height: 1.4em;;
                    color: #888;
                }
            }
        }
        img{
            box-shadow: 0 0px 24px 0 rgba(0, 0, 0, 0.1);
        }
        .row{
            img{
                margin-top: map-get($headerSizes, 1);
                width:33%;
            }
        }
        .column{
            display:flex;
            flex-wrap: wrap;
            > div{
                width:50%;
                &.text-container{
                    &.left{
                        p{
                            padding-right: 15%;
                        }
                    }
                    &.right{
                        .section-title,
                        p{
                            padding-left: 10%;
                        }
                    }
                    margin:auto;
                    .section-title{
                        margin-top:  -100px;
                    }
                }
                &.image-container{
                    img{
                        float: right;
                        width:90%;
                    }
                }

            }
        }
    }
}

@media screen and (max-width: $startMobile) {
    .section-container{
        margin-top: map-get($headerSizes, 1);
        margin-bottom: map-get($headerSizes, 1);
        padding-bottom: map-get($headerSizes, 1);
        .section-body{
            .column{
                div{
                    width:100%;
                    &.text-container{
                        &.right{
                            order:1 !important;
                        }
                        &.left,
                        &.right{
                            .section-title,
                            p{
                                padding:0px;
                            }
                        }
                        .section-title{
                            margin-top:0px;
                            font-size: 0.8em;
                        }
                    }
                    &.image-container{
                        order:1;
                        img{
                            margin-top: $common-pad;
                            width:100%;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: $startSMMobile) {
    .section-container{
        margin-top: map-get($headerSizes, 5);
        .section-body{
            .column{
                div{
                    &.text-container{
                        &.right{
                            order:1 !important;
                        }
                        &.left,
                        &.right{
                            .section-title{
                                font-size: 0.7em;
                            }
                        }
                    }
                }
            }
        }
    }
}